const DirectorsGallery = () => {
	return(
		<section>
			<Row>
				<h1 className={"m-2em"}>Our Team</h1>
			</Row>
			<Row className="md-3">
				<Col md>
					<Card 
					title={teamDescription.P1.Name} 
					subtitle={teamDescription.P1.Role} 
					image={teamDescription.P1.Avatar} 
					text={teamDescription.P1.Long} 
					url="https://quasiscience.com"
					facebook="test"
					linkedin="test"
					/>
				</Col>
				<Col md>
					<Card 
					title={teamDescription.P2.Name} 
					subtitle={teamDescription.P2.Role}  
					image={teamDescription.P2.Avatar} 
					text={teamDescription.P2.Long} 
					url="https://example.com"
					/>
				</Col>
				<Col md>
					<Card 
					title={teamDescription.P3.Name} 
					subtitle={teamDescription.P3.Role} 
					image={testImage} 
					text={teamDescription.P3.Long} 
					url="https://example.com"
					/>
				</Col>
			</Row>
		</section>
	)
};

const About = () => {
	const data = useStaticQuery(
    graphql`
		query{
			file(relativePath: { eq: "images/covers/Group_Silhouettes_Dusk.jpg" }) {
			childImageSharp {
			fluid(maxWidth: 960, maxHeight: 450) {
				...GatsbyImageSharpFluid
			}
			}
		}
		}
	`
	)

	return (
    <Layout>
		<Metadata 
		title="Our Team"
		description="Hope Labs was founded by three friends with similar values and the desire to give back to the community."
		/>
		<section role="main">
			<Row className={`${teamStyles.imageBox} no-margin`}>
				<div className={`${teamStyles.imageFilter} d-none d-sm-block`}></div> 
				<Img fluid={data.file.childImageSharp.fluid}  alt="Artistic cover image"/>
				<h1 className={`${teamStyles.overlayTitle} d-none d-sm-block`}>{"We make a great team!"}</h1>
			</Row>
            <ValuesPart />
			<DirectorsGallery />
		</section>
    </Layout>
	)
}

export default About
